<template>
  <div class="link_wrap">
    <h2 class="friend_link_top"><i class="el-icon-link">ÂèãÊÉÖÈìæÊé•</i></h2>
    <el-tabs v-model="activeName">
      <el-tab-pane label="Áî≥ËØ∑ÂèãÈìæ" name="first">
        <p style="text-align: center; margin-bottom: 20px">
          ÂèãÈìæÁî≥ËØ∑-ËØ∑Á¨¶ÂêàËßÑËåÉüòä
        </p>
        <el-form
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
          label-width="100px"
        >
          <el-form-item label="ÂçöÂÆ¢ÂêçÁß∞" prop="blogName">
            <el-input
              type="text"
              v-model="ruleForm.blogName"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item label="ÂçöÂÆ¢Âú∞ÂùÄ" prop="blogOrigin">
            <el-input
              type="text"
              v-model="ruleForm.blogOrigin"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item label="logoÂú∞ÂùÄ" prop="blogLogo">
            <el-input
              type="text"
              v-model="ruleForm.blogLogo"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item label="ÂçöÂÆ¢ÊèèËø∞" prop="blogMessage">
            <el-input
              type="text"
              v-model="ruleForm.blogMessage"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')"
              >Êèê‰∫§</el-button
            >
            <el-button @click="resetForm('ruleForm')">ÈáçÁΩÆ</el-button>
          </el-form-item>
        </el-form>
      </el-tab-pane>
      <el-tab-pane label="ÂèãÈìæÂ±ïÁ§∫" name="second">
        <Link :links="links"/>
      </el-tab-pane>
      <el-tab-pane label="Êé®ËçêÂèãÈìæ" name="third">
        <Link :links="hotLinks"/>
      </el-tab-pane>
      <el-tab-pane label="‰∏™‰∫∫ÂèãÈìæ" name="fourth">
        <p style="text-align:center">
          <el-button type="primary" @click="activeName = 'first'">ÁÇπÂáªËøõÂÖ•</el-button>
        </p>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import Link from '../components/linkBase/linkChild.vue'
export default {
  name: "Links",
  components:{Link},
  data() {
    let blogNameRule = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("‰∏çËÉΩ‰∏∫Á©∫ÔºÅ"));
      } else {
        callback();
      }
    };
    let blogOriginRule = (rule, value, callback) => {
      if (value.trim() == "") {
        return callback(new Error("‰∏çËÉΩ‰∏∫Á©∫ÔºÅ"));
      } else if (!value.startsWith("http")) {
        return callback(new Error("Ê†ºÂºèËæìÂÖ•‰∏çÊ≠£Á°ÆÔºÅ"));
      } else {
        callback();
      }
    };
    return {
      activeName: "first",
      links: [],
      hotLinks: [],
      ruleForm: {
        blogName: "",
        blogOrigin: "",
        blogLogo: "",
        blogMessage: "",
      },
      rules: {
        blogName: [{ validator: blogNameRule, trigger: "blur" }],
        blogOrigin: [{ validator: blogOriginRule, trigger: "blur" }],
        blogLogo: [{ validator: blogOriginRule, trigger: "blur" }],
        blogMessage: [{ validator: blogNameRule, trigger: "blur" }],
      },
    };
  },
  methods: {
    // Êèê‰∫§Ë°®Âçï
    submitForm(ruleForm) {
      this.$refs[ruleForm].validate(async (valid) => {
        if (!valid) return;
        if (!this.$store.state.userInfo._id)
          return this.$message.error("ËØ∑ÁôªÂΩï");
        const { data: res } = await this.$http.post("/user/userLink", {
          ruleForm: this.ruleForm,
        });
        this.$message.success(res.value);
        this.links.unshift(res.data);
      });
    },
    // ÈáçÁΩÆË°®Âçï
    resetForm(ruleForm) {
      this.$refs[ruleForm].resetFields();
    },
    // Ëé∑ÂèñlinkÊï∞ÊçÆ
    async getLinks() {
      const { data: res } = await this.$http.get("/user/getlinks");
      if(res.code!==200) return
      this.links = res.data;
      this.hotLinks = res.data.filter((item) => item.blogLinkHot);
      this.$refs.ruleForm.resetFields()
    },
  },
  created() {
    this.getLinks()
  },
};
</script>

<style lang="less" scoped>
.link_wrap {
  .friend_link_top {
    background-color: #eee;
    padding: 30px;
    border-radius: 10px;
  }
  
}
</style>